.Solutions {
  min-width: 640px;
}

.Solutions ul {
  display: flex;
  flex-wrap: wrap;
  margin-top: -38px;
  margin-left: -38px;
}

.Solutions ul li {
  position: relative;
  display: flex;
  width: 33.333333%;
  margin-top: 38px;
  padding: 0 0 20px 38px;
}

.Solutions ul li::after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 38px;
  height: 1px;
  content: '';
  background: #eee;
}

.Solutions ul li .iconWrap {
  flex: 0 0 34px;
  margin-right: 28px;
}

.Solutions ul li .iconWrap i {
  font-size: 34px;
}

.Solutions ul li .textWrap {
  flex: 1;
}

.Solutions ul li .textWrap .title {
  font-size: 18px;
  line-height: 1.2;
  margin: 0;
}

.Solutions ul li .textWrap .des {
  line-height: 1.5;
  margin: 10px 0 0;
  color: #999;
}

@media screen and (max-width: 980px) {
  .badCases ul li {
    width: 50%;
  }
}

@media screen and (max-width: 720px) {
  .badCases ul li {
    width: 100%;
  }
}