.quality {
  background: #f3f8fe;
}

.quality .line {
  display: flex;
}

.quality .line img {
  height: 290px;
}

.quality .line:first-child {
  flex-direction: row-reverse;
}

.quality .line + .line {
  margin-top: 2px;
}

.quality .line .listWrap {
  min-height: 290px;
  flex: 1;
  padding: 30px 30px 30px;
  background: #fff;
}

.quality .line .listWrap .title {
  font-size: 18px;
  position: relative;
  display: inline-block;
  padding-bottom: 6px;
  color: #777;
  border-bottom: 4px solid #777;
}

.quality .line .listWrap .bgIcon {
  position: absolute;
  top: 20px;
  right: 20px;
}

.quality .line .listWrap .desc {
  word-break: break-all;
  line-height: 22px;
  margin-top: 18px;
  color: #777;
}

@media screen and (max-width: 1080px) {
  .quality .line {
    display: block;
  }

  .quality .line .matchImg {
    width: 100%;
    height: auto;
    margin: 0 !important;
  }
}