.clothing .line {
  display: flex;
  align-items: stretch;
  margin-top: 2px;
}

.clothing .line .matchImg {
  margin: 0 2px 0 0;
}

.clothing .line:first-child {
  margin-top: 0;
}

.clothing .line:nth-child(2n + 1) {
  flex-direction: row-reverse;
}

.clothing .line:nth-child(2n + 1) .matchImg {
  margin: 0 0 0 2px;
}

.clothing .line .listWrap {
  flex: 1;
  padding: 30px 30px 10px;
  background: #f3f8fe;
}

.clothing .line .listWrap .title {
  font-size: 18px;
  display: inline-block;
  padding-bottom: 8px;
  color: #1976ef;
  border-bottom: 2px solid #1976ef;
}

.clothing .line .listWrap .list {
  overflow: hidden;
  margin-top: 10px;
}

.clothing .line .listWrap .list .item {
  line-height: 22px;
  position: relative;
  margin-top: 16px;
  padding-left: 18px;
  text-align: justify;
}

.clothing .line .listWrap .list .item i,
.clothing .line .listWrap .list .item span {
  font-size: 12px;
  position: absolute;
  top: 0;
  left: 0;
}

.clothing .line .listWrap .list .item span {
  font-size: 18px;
}

@media screen and (max-width: 1180px) {
  .clothing .line {
    display: block;
  }

  .clothing .line .matchImg {
    display: block;
    width: 100%;
    height: auto;
    margin: 0 !important;
  }

  .clothing .line .listWrap {
    padding: 30px;
  }
}