.Products {
  background-size: cover;
  padding-top: 50px;
}

.Products :global .center-block {
  padding: 30px 20px 50px;
  position: relative;
  z-index: 10;
}

.Products.withBanner {
  position: relative;
  padding-top: 100px;
  background: url('@/static/images/solution/bg04.jpg') no-repeat bottom center;
  background-size: cover;
}

.Products.withBanner .bannerContainer {
  background: rgba(255, 255, 255, 0.25);
}

.Products.withBanner::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.Products.gray {
  background: #f3f8fe;
}

.Products .boxItem {
  padding-top: 20px;
  width: 500px;
  background: #fff;
  transition: all ease 0.3s;
  cursor: pointer;
  border-radius: 2px;
}

.Products .boxItem + .boxItem {
  margin-left: 10px;
}

.Products .boxItem:hover {
  box-shadow: 0 20px 20px 0 rgba(25, 118, 239, 0.25), inset 0 -4px 0 0 #1976ef;
}

.Products .boxItem:hover .title {
  color: #1976ef;
}

.Products .boxItem .image {
  display: block;
  margin: 0 20px 20px;
  width: 260px;
  height: 160px;
}

.Products .boxItem .box ul {
  padding: 30px;
  height: 300px;
  overflow: auto;
}

.Products .boxItem .title {
  margin: 0 30px;
  font-size: 18px;
  font-weight: bold;
  line-height: 1;
  display: inline-block;
  padding-bottom: 8px;
  color: #1976ef;
  border-bottom: 2px solid #1976ef;
}

.Products .boxItem .descItem {
  font-size: 14px;
  line-height: 24px;
  position: relative;
  margin-top: 10px;
  padding-left: 20px;
  word-break: break-all;
}

.Products .boxItem .descItem i,
.Products .boxItem .descItem span {
  font-size: 12px;
  position: absolute;
  top: 0;
  left: 0;
}

.Products .boxItem .descItem span {
  font-size: 18px;
}

.bannerContainer {
  background: rgba(25, 118, 239, 0.1);
  border-radius: 6px;
  padding: 50px 0;
  display: flex;
}

.bannerContainer .leftButton,
.bannerContainer .rightButton {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  cursor: pointer;
}

.bannerContainer .leftButton:hover img,
.bannerContainer .rightButton:hover img {
  opacity: 1;
}

.bannerContainer .leftButton img,
.bannerContainer .rightButton img {
  opacity: 0.5;
  width: 60px;
}

.bannerContainer .rightButton img {
  transform: rotate(180deg);
}

.bannerContainer .bannerList {
  width: 920px;
  overflow: hidden;
}

.bannerContainer .cases {
  position: relative;
  display: flex;
  left: 0;
}