.Introduce .right {
  line-height: 25px;
  text-align: justify;
}

.Introduce .right::after {
  display: block;
  clear: both;
  overflow: hidden;
  height: 0;
  content: '';
}

.Introduce .matchImg {
  float: right;
  margin: 0 0 15px 27px;
}

.Introduce :global .center-block {
  padding-bottom: 85px;
}

@media screen and (max-width: 1024px) {
  .aboutUs .matchImg {
    display: block;
    float: none;
    width: 100%;
    height: auto;
    margin: 0 0 15px;
  }
}