@charset 'utf-8';

* {
  box-sizing: border-box;
}

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  margin: 0;
  padding: 0;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
}

input,
button,
select,
textarea {
  font-size: 100%;
  line-height: 1.3;
  color: #333;
  outline: none;
}

textarea {
  overflow: auto;
  resize: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

fieldset,
img {
  border: 0;
}

iframe {
  display: block;
}

abbr,
acronym {
  font-variant: normal;
  border: 0;
}

del {
  text-decoration: line-through;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-weight: 500;
  font-style: normal;
}

ol,
ul {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: 500;
}

q:before,
q:after {
  content: '';
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

a {
  text-decoration: none;
  color: #1daba7;
}

a:hover,
a:active,
a:visited,
a:focus {
  text-decoration: none;
}

/* reset outline */

a:focus,
input:focus {
  outline: none;
}

input::-moz-focus-inner,
button::-moz-focus-inner {
  outline: none;
}

/*for mozilla*/

/* reset firefox input width */

input::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

/* reset placeholder */

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #959595 !important;
}

input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #959595 !important;
}

/* Firefox 18- */

input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #959595 !important;
}

/* Firefox 19+ */

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #959595 !important;
}

:global .clearfix:after {
  font-size: 0;
  display: block;
  visibility: hidden;
  clear: both;
  height: 0;
  content: ' ';
}

:global .clearfix {
  zoom: 1;
}