.Advantages {
  background: #fff;
}

.Advantages ul {
  display: flex;
  flex-wrap: wrap;
  margin-top: -20px;
  margin-left: -17px;
}

.Advantages ul li {
  width: 33.333333%;
  min-height: 330px;
  padding: 20px 0 0 17px;
}

.Advantages ul li .content {
  height: 100%;
  padding: 30px 0;
  transition: all ease 0.3s;
  background: #f3f8fe;
  border: 1px solid #f3f8fe;
  border-radius: 2px;
}

.Advantages ul li .content:hover {
  transform: translate3d(0, -10px, 0);
  box-shadow: 0 20px 20px 0 rgba(25, 118, 239, 0.25), inset 0 -4px 0 0 #1976ef;
}

.Advantages ul li .content:hover .title {
  color: #1976ef;
}

.Advantages ul li .icon {
  display: block;
  width: 100px;
  height: 115px;
  margin: auto;
  background-image: url('@/static/images/product/icon01.png');
}

.Advantages ul li .title {
  font-size: 18px;
  line-height: 1.5;
  margin-top: 22px;
  padding: 0 10%;
  text-align: center;
}

.Advantages ul li .des {
  line-height: 24px;
  margin-top: 15px;
  padding: 0 10%;
  text-align: center;
  color: #666;
}

.Advantages ul li .des .textCenter {
  display: block;
  text-align: center;
}

@media screen and (max-width: 960px) {
  .service ul li {
    width: 50%;
  }
}

@media screen and (max-width: 740px) {
  .service ul li {
    width: 100%;
  }
}