.Ability {
  min-width: 640px;
  background: #f3f8fe;
}

.Ability :global .center-block {
  padding-bottom: 0;
}

.Ability .line {
  display: flex;
  margin-top: 2px;
}

.Ability .line .imageContainer {
  background-color: #f7f7f7;
  width: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Ability .line .imageContainer img {
  width: 350px;
  height: 200px;
}

.Ability .line:first-child {
  flex-direction: row-reverse;
  margin-top: 0;
}

.Ability .line .listWrap {
  position: relative;
  flex: 1;
  padding: 30px;
  color: #777;
  background: #fff;
}

.Ability .line .listWrap .title {
  font-size: 18px;
  font-weight: bold;
  position: relative;
  display: inline-block;
}

.Ability .line .listWrap .title::after {
  position: absolute;
  right: 0;
  bottom: -10px;
  left: 0;
  height: 4px;
  content: '';
  background: #777;
}

.Ability .line .listWrap .bgIcon {
  position: absolute;
  top: 20px;
  right: 20px;
}

.Ability .line .listWrap .list {
  padding: 10px 0;
}

.Ability .line .listWrap .list .item {
  word-break: break-all;
  line-height: 22px;
  position: relative;
  margin-top: 16px;
  padding-left: 22px;
}

.Ability .line .listWrap .list .item i {
  font-size: 12px;
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
}

@media screen and (max-width: 1170px) {
  .product .line {
    display: block;
  }

  .product .line .matchImg {
    width: 100%;
    height: auto;
    margin: 0 !important;
  }
}