.navigator {
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  overflow: hidden;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 30px;
  transition: all ease 0.5s;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.navigator.notTop {
  border-bottom-color: #fff;
  background: #fff;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.15);
}

.navigator.notTop .menus a {
  color: #777;
}

.navigator.notTop .menus a.active {
  font-weight: bold;
  color: #333;
}

.navigator.notTop .menus a.active:after {
  width: 100%;
  background: #333;
}

.navigator.notTop .menus a:hover {
  color: #999;
}

.navigator.notTop .menus a:hover:after {
  width: 100%;
}

.navigator.notTop .logo .sub {
  color: #777;
}

.navigator.notTop .phone {
  color: #777;
}

.logo {
  padding: 18px 0;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.logo .tdlLogo {
  width: 110px;
  margin-right: 10px;
  color: #fff;
}

.logo .sub {
  position: relative;
  font-size: 22px;
  vertical-align: text-bottom;
  color: #fff;
}

.menus {
  margin: auto;
  white-space: nowrap;
}

.menus a {
  font-size: 16px;
  line-height: 60px;
  position: relative;
  display: inline-block;
  cursor: pointer;
  user-select: none;
  color: #fff;
}

.menus a::after {
  position: absolute;
  bottom: 5px;
  left: 50%;
  display: inline-block;
  width: 0;
  height: 4px;
  content: '';
  transition: all ease 0.3s;
  transform: translate3d(-50%, 0, 0);
  background: #fff;
}

.menus a + a {
  margin-left: 50px;
}

.menus a.active {
  font-weight: bold;
}

.menus a.active::after {
  width: 100%;
}

.menus a:hover::after {
  width: 100%;
}

.phone {
  font-size: 22px;
  font-weight: bold;
  line-height: 60px;
  width: 300px;
  text-align: right;
  text-decoration: none;
  color: #fff;
}

.phone > i {
  font-size: 20px;
  margin-right: 8px;
  vertical-align: top;
}

@media screen and (max-width: 1250px) {
  .menus {
    margin: 0 0 0 auto;
  }

  .phone {
    display: none;
  }
}

@media screen and (max-width: 1000px) {
  .sub {
    display: none;
  }
}