.partner {
  min-width: 640px;
}

.partner ul {
  font-size: 0;
  line-height: 0;
}

.partner ul li {
  position: relative;
  display: inline-block;
  box-sizing: content-box;
  width: 159px;
  height: 99px;
  text-align: center;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
}

.partner ul li::before {
  position: absolute;
  top: -1px;
  right: 0;
  left: 0;
  height: 1px;
  content: '';
  background: #eee;
}

.partner ul li::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -1px;
  width: 1px;
  content: '';
  background: #eee;
}

.partner ul li:hover .title {
  opacity: 0.9;
}

.partner ul li .campanyLogo {
  display: block;
  width: 100%;
  height: 100%;
}

.partner ul li .title {
  font-size: 14px;
  line-height: 34px;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 34px;
  transition: all ease 0.3s;
  text-align: center;
  opacity: 0;
  color: #fff;
  background-color: #1976ef;
}