.pageFooter {
  position: relative;
  min-width: 640px;
  color: #fff;
  background: #013f90;
}

.pageFooter .icon {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  border-radius: 100%;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 1px 0 20px 6px rgba(25, 118, 239, 0.2);
}

.pageFooter .icon .tdlLogo {
  width: 80px;
}

.pageFooter .top::after,
.pageFooter .bottom::after {
  display: block;
  clear: both;
  overflow: hidden;
  height: 0;
  content: '';
}

.pageFooter .left {
  float: left;
  width: 65%;
}

.pageFooter .right {
  float: right;
  width: 35%;
  text-align: right;
}

.pageFooter .top {
  padding: 15px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.12);
}

.pageFooter .top .links {
  line-height: 44px;
}

.pageFooter .top .links li {
  display: inline-block;
  margin-right: 30px;
}

.pageFooter .top .links li a {
  color: inherit;
}

.pageFooter .top .phone {
  font-size: 22px;
  line-height: 1;
}

.pageFooter .top .phone i {
  font-size: 20px;
  margin-right: 8px;
  color: #fff;
}

.pageFooter .top .phone a {
  color: inherit;
}

.pageFooter .top .workTime {
  font-size: 12px;
  line-height: 1;
  margin-top: 10px;
  color: rgba(255, 255, 255, 0.5);
}

.pageFooter .bottom {
  line-height: 1.5;
  padding: 30px 0;
}

.pageFooter .bottom .desc {
  font-size: 12px;
  margin-top: 6px;
  color: rgba(255, 255, 255, 0.5);
}

.pageFooter .bottom .copyright {
  font-size: 12px;
  margin-top: 15px;
  color: rgba(255, 255, 255, 0.5);
}

.pageFooter .bottom .copyright a {
  color: inherit;
}

.pageFooter .bottom .qrCodeDes {
  font-size: 12px;
  line-height: 1.83;
  display: inline-block;
  vertical-align: middle;
  color: rgba(255, 255, 255, 0.5);
}

.pageFooter .bottom .qrCode {
  width: 76px;
  height: 76px;
  margin-left: 10px;
  vertical-align: middle;
}

@media screen and (max-width: 800px) {
  .pageFooter .top {
    padding-top: 40px;
  }
}