@import '//at.alicdn.com/t/font_1170667_0cyh0bejzssk.css';

html {
  font-family: -apple-system, BlinkMacSystemFont, PingFang SC, Helvetica, Tahoma, Arial, Microsoft YaHei, Heiti, sans-serif, SimSun, serif;
  font-size: 14px;
  min-width: 740px;
  word-break: break-word;
  color: #333;
}

:global .center-block {
  display: flex;
  min-width: 740px;
  max-width: 1240px;
  margin: auto;
  padding: 100px 20px;
}

:global .center-block .block-left {
  flex: 0 0 200px;
}

:global .center-block .block-right {
  flex: 1;
  padding-left: 38px;
}

:global .center {
  min-width: 740px;
  max-width: 1240px;
  margin: auto;
  padding-right: 20px;
  padding-left: 20px;
}

:global #cvd-open {
  display: none;
}

/* 滚动条样式 */

::-webkit-scrollbar-thumb {
  background: hsla(0, 0%, 73%, 0.8);
  background-clip: padding-box;
  border: 2px solid transparent;
  border-radius: 10px;
}

::-webkit-scrollbar-button {
  height: 4px;
  background-color: rgba(0, 0, 0, 0);
}

::-webkit-scrollbar {
  width: 10px !important;
  height: 10px;
}