.platform {
  min-width: 640px;
  background: #f3f8fe;
}

.platform ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.platform ul li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 23%;
  height: 240px;
  margin: 0 2% 4% 0;
  padding: 20px;
  transition: all ease 0.3s;
  text-align: center;
  background-color: #fff;
}

.platform ul li img {
  width: 60px;
}

.platform ul li:last-child {
  margin-right: 0;
}

.platform ul li:hover {
  transform: translate3d(0, -10px, 0);
  box-shadow: 0 20px 20px 0 rgba(25, 118, 239, 0.25), inset 0 -4px 0 0 #1976ef;
}

.platform ul li:hover .title {
  color: #1976ef;
}

.platform ul li .title {
  font-size: 18px;
  font-weight: bold;
  margin-top: 20px;
  color: #333;
}

.platform ul li .des {
  margin-top: 16px;
  text-transform: uppercase;
  color: rgba(102, 102, 102, 0.75);
}

@media screen and (max-width: 900px) {
  .platform ul li {
    width: 48%;
  }

  .platform ul li:nth-child(2n) {
    margin-right: 0;
  }
}