.Types {
  min-width: 640px;
}

.Types .box {
  padding: 10px 0;
  background: #f3f8fe;
}

.Types ul {
  display: flex;
  flex-wrap: wrap;
}

.Types ul li {
  width: 33.33333%;
  margin: 30px 0;
  text-align: center;
}

.Types ul li:hover .title {
  color: #1976ef;
}

.Types ul li .icon {
  display: inline-block;
  height: 115px;
}

.Types ul li .icon img {
  width: 200px;
}

.Types ul li .title {
  font-size: 18px;
  font-weight: bold;
  margin-top: 22px;
  color: #333;
}

.Types ul li .des {
  max-width: 200px;
  word-break: break-all;
  line-height: 24px;
  margin-top: 15px;
  margin: 15px auto;
  color: #666;
}

@media screen and (max-width: 960px) {
  .service ul li {
    width: 50%;
  }
}

@media screen and (max-width: 720px) {
  .service ul li {
    width: 100%;
  }
}