.contactContainer {
  min-width: 640px;
  background: #f3f8fe;
}

.contactContainer .box {
  position: relative;
  padding: 30px;
  background: #fff;
  box-shadow: 0 20px 20px 0 rgba(25, 118, 239, 0.2);
}

.contactContainer .tip {
  line-height: 1.5;
  margin-bottom: 48px;
  color: #333;
}

.contactContainer .tip a {
  color: inherit;
}

.contactContainer .tip a:hover {
  text-decoration: underline;
}

.contactContainer .flag {
  position: absolute;
  top: 20px;
  right: 20px;
}

.contactContainer .input,
.contactContainer .textarea {
  font-size: 16px;
  border: none;
  border-bottom: 1px solid #ccc;
  background: #fff;
}

.contactContainer .input:focus,
.contactContainer .textarea:focus {
  color: #1976ef;
  border-bottom: 1px solid #1976ef;
}

.contactContainer .inputGroup {
  margin-left: -30px;
}

.contactContainer .input {
  width: 280px;
  height: 50px;
  margin-left: 30px;
}

.contactContainer .textarea {
  line-height: 24px;
  display: block;
  width: 100%;
  height: 160px;
  margin-top: 36px;
}

.contactContainer .btnLine {
  margin-top: 30px;
  text-align: right;
}

.contactContainer .btnLine .button {
  font-size: 16px;
  line-height: 50px;
  display: inline-block;
  width: 200px;
  height: 50px;
  cursor: pointer;
  text-align: center;
  color: #fff;
  border-radius: 2px;
  background: #f90;
  box-shadow: 0 10px 10px 0 rgba(25, 118, 239, 0.2);
}

@media screen and (max-width: 940px) {
  .contactContainer .inputGroup {
    margin: 0;
  }

  .contactContainer .input {
    width: 100%;
    margin: 0;
  }
}