.Services {
  position: relative;
  padding: 130px 0 100px;
  background-image: url('@/static/images/product/bg02.jpg');
  background-size: cover;
}

.Services::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.Services .center {
  display: flex;
  position: relative;
  z-index: 10;
  padding-top: 20px;
}

.Services .left {
  flex: 0 0 200px;
}

.Services .right {
  display: flex;
  overflow: hidden;
  flex: 1;
  flex-wrap: wrap;
  margin-left: 36px;
  background: #fff;
}

.Services .right .title {
  font-size: 18px;
  line-height: 1.25;
  position: relative;
  display: inline-block;
  padding-bottom: 8px;
  border-bottom: 2px solid #1976ef;
}

.Services .right .boxItem {
  font-size: 14px;
  width: 25%;
  margin-top: 25px;
  padding: 0 10px 0 30px;
}

.Services .right .boxItem + li {
  border-left: 1px solid #ebf3f6;
}

.Services .right .boxItem .descList {
  margin-top: 28px;
}

.Services .right .boxItem .descList .descItem {
  line-height: 28px;
  position: relative;
  padding-left: 22px;
  color: #333;
}

.Services .right .boxItem .descList .descItem i {
  font-size: 12px;
  position: absolute;
  top: 0;
  left: 0;
  color: #999;
}

.Services .right .boxItem2 {
  font-size: 14px;
  width: 50%;
  margin-top: 25px;
  padding: 25px 20px 30px 30px;
  border-top: 2px solid #1976ef;
  background: #f3f8fe;
}

.Services .right .boxItem2 .desc {
  line-height: 22px;
  margin-top: 20px;
  text-align: justify;
}

.Services .right .boxItem2 + li {
  border-left: 2px solid #1976ef;
}

@media screen and (max-width: 1024px) {
  .shopkeeper .right .boxItem {
    width: 50%;
  }
}

@media screen and (max-width: 800px) {
  .shopkeeper {
    padding: 180px 0 60px;
  }
}

@media screen and (max-width: 740px) {
  .shopkeeper .right .boxItem {
    width: 100%;
    margin: 30px 0;
  }

  .shopkeeper .right .boxItem2 {
    width: 100%;
    margin-top: 0;
    border-left: none !important;
  }
}