.topBanner {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  flex-direction: column;
  width: 100%;
  height: 80%;
  min-height: 575px;
  background: rgba(0, 0, 0, 0.5);
}

.topBanner .companyName {
  font-size: 18px;
  color: #fff;
}

.topBanner::after {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  opacity: 0.9;
  background: url('@/static/images/bg01.jpg') no-repeat top center;
  background-size: cover;
}

.topBanner .slogan {
  display: block;
  height: 150px;
}

.topBanner .mission {
  font-size: 18px;
  line-height: 1.5;
  margin: 20px auto 0;
  padding: 0 30px;
  text-align: center;
  letter-spacing: 1px;
  color: #fff;
  max-width: 920px;
}

.topBanner .callForm {
  margin: 100px auto 0;
  border-radius: 2px;
}

.topBanner .callForm .input {
  font-size: 18px;
  width: 300px;
  height: 50px;
  padding-left: 20px;
  text-align: left;
  vertical-align: top;
  border: none;
  background: #fff;
}

.topBanner .callForm .button {
  font-size: 18px;
  width: 120px;
  height: 50px;
  padding: 0 14px;
  cursor: pointer;
  vertical-align: top;
  color: #fff;
  border: none;
  background: #f90;
}

.topBanner .callForm .button:hover {
  background: #fa0;
}

.topBanner .callTip {
  font-size: 16px;
  margin: 20px auto auto;
  color: rgba(255, 255, 255, 0.75);
}

.effects {
  position: absolute;
  top: 80%;
  left: 0;
  width: 100%;
  height: 70px;
  background: url('@/static/images/img-dynamic-data.png') center;
  background-size: auto 100%;
}

.effects .center {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.effects .effect {
  font-size: 16px;
  flex: 1;
  max-width: 320px;
  text-align: center;
  color: #333;
}

.effects .effect::before {
  font-size: 16px;
  padding: 0 10px;
  content: '\02022';
  color: #666;
}

.effects .effect .number {
  font-size: 16px;
  padding-left: 5px;
  color: #f90;
}

.effects .split {
  width: 1px;
  height: 14px;
  background: #cdcdcd;
}

.blocks {
  position: absolute;
  top: calc(80% + 70px);
  left: 0;
  width: 100%;
}

@media screen and (max-width: 800px) {
  .topBanner {
    height: calc(100% - 70px);
    min-height: 635px;
    padding-top: 120px;
  }

  .effects {
    top: calc(100% - 70px);
  }

  .blocks {
    top: 100%;
  }
}

@media screen and (max-height: 720px) {
  .topBanner,
  .effects,
  .blocks {
    position: relative;
    top: auto;
    left: auto;
  }
}