.banner {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 500px;
  background: url('@/static/images/about/bg03.jpg') no-repeat center bottom;
  background-size: cover;
}

.banner:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.banner .slogan {
  margin: auto auto 0;
}

.banner .content {
  margin: auto auto 0;
  position: relative;
  padding-top: 60px;
  z-index: 10;
}

.banner .content .title {
  text-align: center;
  color: #fff;
  font-size: 32px;
}

.banner .content .desc {
  max-width: 920px;
  margin-top: 30px;
  color: #fff;
  font-size: 18px;
  text-align: center;
}

.banner .countWrap {
  position: relative;
  z-index: 10;
  display: flex;
  padding: 130px auto 60px;
}

.banner .countWrap .item {
  width: 210px;
  text-align: center;
  color: #fff;
}

.banner .countWrap .item + .item {
  border-left: 1px solid rgba(255, 255, 255, 0.2);
}

.banner .countWrap .item .count {
  font-size: 18px;
}

.banner .countWrap .item .desc {
  margin-top: 10px;
}