.container {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  text-align: center;
}

.container.visible {
  display: block;
}

.container .center {
  position: absolute;
  top: 50%;
  left: 50%;
  display: inline-block;
  padding: 48px 63px;
  transform: translate(-50%, -50%);
  text-align: center;
  border: solid 2px #1976ef;
  background: #fff;
}

.container .center .close {
  line-height: 40px;
  position: absolute;
  top: -2px;
  right: -40px;
  width: 40px;
  height: 40px;
  text-align: center;
  background-color: #1976ef;
}

.container .center .close i {
  font-size: 18px;
  opacity: 0.75;
  color: #fff;
}

.container .center .close:hover {
  cursor: pointer;
  user-select: none;
}

.container .center .close:hover i {
  opacity: 1;
}

.container .center .okIcon {
  display: inline-block;
  width: 90px;
  height: 90px;
  border-radius: 90px;
  background-color: #1976ef;
}

.container .center .okIcon i {
  font-size: 62px;
  line-height: 90px;
  color: #fff;
}

.container .center .title {
  font-size: 18px;
  margin-top: 18px;
  color: #1976ef;
}

.container .center .des {
  line-height: 26px;
  margin-top: 20px;
  color: rgba(51, 51, 51, 0.75);
}