.milestone {
  background: #f3f8fe;
}

.milestone .timeline {
  transition: all ease 0.3s;
}

@media screen and (min-width: 1051px) {
  .milestone :global .block-right {
    position: relative;
    height: 236px;
  }

  .timeline {
    position: absolute;
    top: 0;
    right: 0;
    left: 60px;
    display: flex;
    align-self: flex-start;
    flex-direction: row-reverse;
    justify-content: flex-start;
    height: 236px;
  }

  .timeline::after {
    position: absolute;
    top: 50%;
    right: 0;
    left: -22px;
    height: 3px;
    content: '';
    transform: translate3d(0, -50%, 0);
    border-top: 1px solid #f3f8fe;
    border-bottom: 1px solid #f3f8fe;
    background: #1976ef;
  }

  .timeline::before {
    position: absolute;
    top: 50%;
    left: -20px;
    width: 0;
    content: '';
    transform: translate3d(-50%, -50%, 0);
    border-top: 6px solid transparent;
    border-right: 12px solid #1976ef;
    border-bottom: 6px solid transparent;
    border-left: 0 solid transparent;
    z-index: 100;
  }

  .timeline .item {
    width: 18%;
    text-align: right;
  }

  .timeline .item .itemContent {
    position: relative;
    display: inline-block;
    text-align: left;
  }

  .timeline .item .itemContent::before {
    position: absolute;
    left: -23px;
    width: 1px;
    height: 110px;
    content: '';
    background: #1976ef;
  }

  .timeline .item .year {
    font-size: 18px;
    font-weight: bold;
    line-height: 20px;
    height: 20px;
  }

  .timeline .item .event {
    font-size: 14px;
    line-height: 20px;
    margin-top: 11px;
    max-width: 300px;
    word-break: break-all;
  }

  .timeline .item .anchor {
    position: absolute;
    z-index: 1;
    left: -30px;
    box-sizing: border-box;
    width: 14px;
    height: 14px;
    transform: translate3d(0, -50%, 0);
    border: 3px solid #1976ef;
    border-radius: 50%;
    background: #eef6ff;
  }

  .timeline .item .decorator {
    position: absolute;
    left: -32px;
    box-sizing: border-box;
    width: 18px;
    height: 18px;
    border: 4px solid #cbe2ff;
    border-radius: 50%;
    background: #1976ef;
  }

  .timeline .item:nth-child(2n + 1) {
    align-self: flex-start;
  }

  .timeline .item:nth-child(2n + 1) .itemContent::before {
    top: 5px;
  }

  .timeline .item:nth-child(2n + 1) .anchor {
    top: 1px;
    transform: translate3d(0, 110px, 0);
  }

  .timeline .item:nth-child(2n + 1) .decorator {
    top: 1px;
  }

  .timeline .item:nth-child(2n) {
    align-self: flex-end;
  }

  .timeline .item:nth-child(2n) .itemContent::before {
    bottom: 5px;
  }

  .timeline .item:nth-child(2n) .anchor {
    bottom: 1px;
    transform: translate3d(0, -110px, 0);
  }

  .timeline .item:nth-child(2n) .decorator {
    bottom: 1px;
  }
}

@media screen and (max-width: 1050px) {
  .timeline {
    position: relative;
    margin: 0 19px 0 40px;
    padding: 1px 1px 40px 60px;
  }

  .timeline::before {
    position: absolute;
    bottom: -12px;
    left: -10px;
    width: 5px;
    height: 0;
    content: '';
    border-top: 12px solid rgba(25, 118, 239, 0.5);
    border-right: 6px solid transparent;
    border-bottom: 12px solid transparent;
    border-left: 6px solid transparent;
  }

  .timeline::after {
    position: absolute;
    top: 0;
    left: -4px;
    width: 3px;
    height: 100%;
    content: '';
    border-right: 1px solid #f3f8fe;
    border-left: 1px solid #f3f8fe;
    background: #1976ef;
  }

  .timeline .item {
    margin: 29px 0 51px;
  }

  .timeline .item:last-child {
    margin-bottom: 0;
  }

  .timeline .itemContent {
    position: relative;
    z-index: 1;
  }

  .timeline .itemContent::before {
    position: absolute;
    top: 10px;
    left: -60px;
    width: 37px;
    height: 1px;
    content: '';
    transform: translate3d(0, -50%, 0);
    background: #1976ef;
  }

  .timeline .anchor {
    position: absolute;
    top: 3px;
    left: -69px;
    box-sizing: border-box;
    width: 14px;
    height: 14px;
    border: 3px solid #1976ef;
    border-radius: 50%;
    background: #eef6ff;
  }

  .timeline .decorator {
    position: absolute;
    top: 1px;
    left: -32px;
    box-sizing: border-box;
    width: 18px;
    height: 18px;
    border: 4px solid #cbe2ff;
    border-radius: 50%;
    background: #1976ef;
  }

  .timeline .year {
    font-size: 18px;
    font-weight: bold;
    line-height: 20px;
    position: relative;
    height: 20px;
  }

  .timeline .event {
    font-size: 14px;
    line-height: 20px;
    margin-top: 11px;
  }
}