.rightBar {
  position: fixed;
  z-index: 100;
  top: 50%;
  right: 20px;
  transform: translate3d(0, -50%, 0);
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.1);
}

.rightBar .chat,
.rightBar .phone,
.rightBar .quotation {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 70px;
  height: 70px;
  cursor: pointer;
}

.rightBar .chat > img,
.rightBar .phone > img,
.rightBar .quotation > img {
  height: 20px;
  margin-bottom: 10px;
}

.rightBar .chat > p,
.rightBar .phone > p,
.rightBar .quotation > p {
  font-size: 12px;
  font-weight: bold;
  color: #fff;
}

.rightBar .chat {
  position: relative;
  border-radius: 2px 2px 0 0;
  background: #ff9900;
}

.rightBar .chat:hover {
  background: #fa0;
}

.rightBar .badge {
  position: absolute;
  top: -11px;
  right: -11px;
  width: 22px;
  height: 22px;
  border: 2px solid #ffffff;
  border-radius: 50%;
  background: #ff5500;
}

.rightBar .badge .badgeNum {
  font-size: 12px;
  font-weight: bold;
  line-height: 18px;
  display: flex;
  justify-content: center;
  color: #ffffff;
}

.rightBar .quotation {
  position: relative;
  border-radius: 0;
  background: #2f74ff;
}

.rightBar .quotation .quotationModel {
  position: absolute;
  top: 0;
  left: -310px;
  width: 300px;
  cursor: default;
  transition: all ease 0.6s;
  transform: translate3d(-30px, 0, 0);
  pointer-events: none;
  opacity: 0;
}

.rightBar .quotation:hover {
  background: #4884ff;
}

.rightBar .quotation .quotationModelClick {
  position: absolute;
  top: 0;
  left: -310px;
  width: 300px;
  cursor: default;
  transition: all ease 0.6s;
  transform: translate3d(0, 0, 0);
  pointer-events: auto;
  opacity: 1;
}

.rightBar .quotation .quotationContent {
  position: relative;
  padding: 15px 20px;
  background: #fff;
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.1);
}

.rightBar .quotation .quotationContent .tittle {
  font-size: 18px;
  line-height: 25px;
  margin: 5px 0;
  text-align: center;
  color: #333333;
}

.rightBar .quotation .quotationContent .sign {
  position: absolute;
  top: 65px;
  left: 25px;
  width: 30px;
  height: 30px;
  background: url('@/static/images/ico-widget-mobile.png') no-repeat center;
  background-size: contain;
}

.rightBar .quotation .quotationContent .input {
  font-size: 18px;
  width: 100%;
  height: 50px;
  margin: 5px 0;
  padding: 0 20px 0 40px;
  border: 1px solid #cccccc;
  border-radius: 2px;
  background: #fff;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1);
}

.rightBar .quotation .quotationContent .button {
  font-size: 18px;
  width: 100%;
  height: 40px;
  margin: 5px 0;
  padding: 0 14px;
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 2px;
  background: #f90;
}

.rightBar .quotation .quotationContent .button:hover {
  background: #fa0;
}

.rightBar .quotation .quotationContent:after {
  position: absolute;
  top: 30px;
  right: -16px;
  width: 0;
  height: 0;
  content: '';
  border: 8px solid transparent;
  border-left-color: #fff;
}

.rightBar .phone {
  position: relative;
  background: #8859e6;
}

.rightBar .phone .phoneModel {
  position: absolute;
  top: 0;
  left: -198px;
  overflow: hidden;
  width: 200px;
  height: 70px;
  cursor: default;
  transition: all ease 0.6s;
  transform: translate3d(-30px, 0, 0);
  pointer-events: none;
  opacity: 0;
}

.rightBar .phone:hover {
  background: #9367ed;
}

.rightBar .phone:hover .phoneModel {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.rightBar .phone .phoneContent {
  font-size: 22px;
  line-height: 70px;
  text-align: center;
  color: #fff;
  background: #9367ed;
}

.rightBar .phone .phoneContent > i {
  font-size: 20px;
  margin-right: 8px;
}