.heading.white .title,
.heading.white .des,
.heading.white .en {
  color: #fff;
}

.heading.white .split {
  background-color: #fff;
}

.heading .title {
  font-size: 28px;
  line-height: 44px;
  color: #333;
}

.heading .title b {
  font-weight: bold;
}

.heading .des {
  font-size: 14px;
  line-height: 25px;
  color: #999;
}

.heading .split {
  width: 35px;
  height: 4px;
  margin-top: 30px;
  background-color: #1976ef;
}

.heading .en {
  font-size: 16px;
  line-height: 26px;
  margin-top: 23px;
  text-transform: uppercase;
  color: rgba(51, 51, 51, 0.75);
}